import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import { AnimatedSwitch } from 'react-router-transition';
import Details from './Details';
import Logo from '../../imgs/personagens.png';
import '../../App.css';
import DataPersonagens from '../../DataPersonagens';
import Card from './Card';

function Personagens() {
  return (
    <Router>
      <AnimatedSwitch
        atEnter={{ opacity: 0 }}
        atActive={{ opacity: 1 }}>
        
      <div className="Personagens">
        <img src={Logo} alt="logo" />
        <p className="Text-med"> 
            So mais de 30 Personagens disponveis, todos possuem Jutsus prprios de Ataque, Buff e no mnimo 1 Jutsu Especial.
            Os Jutsus Especiais de cada personagem podem ser uma Cura, Invocao, Jutsu de Ataque, Trap, Buff ou outros.
        </p>
          <section>{DataPersonagens.map((data) => <Card key={data.id} data={data} />)}
          <Route exact path="/personagem/:id" component={Details} />  
          </section>
        </div>
      </AnimatedSwitch>
    </Router>
  );
}

export default Personagens;
